<template>
  <div class="wsy_box">
    <!-- 顶部导航栏 -->
    <div class="header_container">
      <div class="header_wrapper">
        <div class="head_left">
          <van-icon name="arrow-left" @click="onClickLeft" />
        </div>
        <div v-show="!wsy_topFlag" class="head_title">课程详情</div>
        <div class="head_titleA" v-show="wsy_topFlag">
          <p
            :class="[active == index ? 'titleActive' : '']"
            v-for="(item, index) in wsy_top"
            :key="index"
            @click="wsy_scr(index)"
          >
            {{ item }}
          </p>
        </div>
        <div class="head_icon" @click="onClickRight">
          <img
            src="data:image/png;base64,"
          />
        </div>
      </div>
    </div>
    <!-- 顶部导航栏 -->
    <div class="wsy_info" ref="scrollBox">
      <div class="scrollBox">
        <div class="course_info" ref="lwh_ke1">
          <div class="info_title">
            <p>{{ CourseInfo.title }}</p>
            <van-icon
              v-show="CourseInfo.is_collect == 0"
              name="star-o"
              color="#6C6C6C"
              @click="course(1)"
            />
            <van-icon
              v-show="CourseInfo.is_collect == 1"
              name="star"
              color="#FC5500"
              @click="course(0)"
            />
          </div>
          <div class="info_price" v-show="CourseInfo.price == 0">免费</div>
          <!-- <div class="info_price" v-show="CourseInfo.price!=0">{{ CourseInfo.price }}</div> -->
          <span class="wsy_price" v-show="CourseInfo.price != 0">
            <img
              src="https://msmk2019.oss-cn-shanghai.aliyuncs.com/uploads/image/20191HHDExgz0u1567065946.png"
            />
            <span>{{ CourseInfo.price | toFixed }}</span>
          </span>
          <div class="info_classify">
            共
            <span>{{ CourseInfo.total_periods }}</span
            >课时 <span>|</span>&nbsp; <span>{{ CourseInfo.sales_num }}</span
            >人已报名
          </div>
        </div>
        <div class="teach_team" ref="lwh_ke2">
          <div>教学团队</div>
          <div class="teach_wrapper">
            <div class="teach_left">
              <img :src="lwh_list.teacher_avatar" alt />
              <span>{{ lwh_list.teacher_name }}</span>
            </div>
            <div class="teach_right"></div>
          </div>
        </div>
        <div class="classify_jieshao" ref="lwh_ke3">
          <p class="title">课程介绍</p>
          <p class="font">{{ CourseInfo.course_statement }}</p>
        </div>
        <div class="classify_outline" ref="lwh_ke4">
          <p>课程大纲</p>
          <div class="classify_slide" @click='jumpVideo'>
            <div class="dian"></div>
            <div class="classify_slide_right">
              <!-- <div class="classify_slide_right_top">
                <div class="classify_playback">回放</div>
                <div class="classify_title">第一讲第一课时</div>
              </div>
              <div class="classify_slide_right_bottom">
                <span>{{ data.username }}</span>
                <span>03月20日 08:00 - 10:30</span>
              </div>-->
              <p>自主招生冲刺讲座6-多元方程组与可转化为多元方程组问题</p>
            </div>
          </div>
        </div>
        <div class="classify_comment" ref="lwh_ke5">
          <p>课程评论</p>
          <div class="comment_wrapper">
            <div
              class="comment_slide"
              v-for="(item, index) in commentData"
              :key="index"
              v-show="commentData.length > 1"
            >
              <div class="slide_top">
                <div class="slide_left">
                  <img :src="item.avatar" alt />
                  <div class="slide_center">
                    <div class="slideCenter_top">
                      <span>{{ item.nickname }}</span>
                      <van-rate
                        v-model="item.grade"
                        size="14px"
                        gutter="2px"
                        color="#EA7A2F"
                        readonly
                      />
                    </div>
                  </div>
                </div>

                <div class="slide_right">{{ item.created_at | moment }}</div>
              </div>
              <div class="slideCenter_bottom">
                <span>{{ item.content }}</span>
              </div>
            </div>
            <div class="comment_none" v-show="commentData.length == 0">
              <img src="https://wap.365msmk.com/img/empty.0d284c2e.png" alt />
              <div>暂无评论</div>
            </div>
            <div class="emit"></div>
          </div>
        </div>
      </div>

      <!-- 分享弹出层 -->
      <van-popup round v-model="PopupIsShow">
        <div class="wsy_popup">
          <p>分享</p>
          <!-- <img
            src="data:image/png;base64,"
          /> -->
          <vue-qr :text="qr_text" :size="200"></vue-qr>
        </div>
      </van-popup>
      <!-- 分享弹出层 -->
    </div>
    <div
      class="wsy_bottom"
      v-show="CourseInfo.is_join_study != 0"
      @click="toStudy"
    >
      立即学习
    </div>
    <div
      class="wsy_bottom"
      v-show="CourseInfo.is_join_study == 0"
      @click="toPurchase"
    >
      立即报名
    </div>
  </div>
</template>

<script>
import BetterScroll from "better-scroll";
import { Toast } from "vant";
import vueQr from "vue-qr";
export default {
  name: "",
  components: {
    vueQr,
  },
  props: {},
  data() {
    return {
      qr_text:window.location.href, //分享二维码
      course_type: null,
      PopupIsShow: false,
      wsy_bs: null,
      gradeValue: 3,
      wsy_topFlag: false,
      wsy_top: ["课程介绍", "课程大纲", "课程评价"],
      active: 0,
      commentData: [], //评论数据
      CourseInfo: {}, //课程详情数据
      wsy_Flag: null,
      lwh_list:{}
    };
  },
  created() {},
  mounted() {
    console.log(this.qr_text);
    
    this.$nextTick(() => {
      this.wsy_bs = new BetterScroll(this.$refs.scrollBox, {
        probeType: 3,
        click: true,
      });

      this.wsy_bs.on("scroll", (pos) => {
        if (pos.y <= -100) {
          this.wsy_topFlag = true;
        } else {
          this.wsy_topFlag = false;
        }
        if (pos.y < -(this.$refs.lwh_ke1.clientHeight+this.$refs.lwh_ke2.clientHeight) && pos.y > -(this.$refs.lwh_ke1.clientHeight+this.$refs.lwh_ke2.clientHeight+this.$refs.lwh_ke3.clientHeight)-30) {
          this.active = 0;
        } else if (pos.y < -(this.$refs.lwh_ke1.clientHeight+this.$refs.lwh_ke2.clientHeight+this.$refs.lwh_ke3.clientHeight)-30 && pos.y > -(this.$refs.lwh_ke1.clientHeight+this.$refs.lwh_ke2.clientHeight+this.$refs.lwh_ke3.clientHeight+this.$refs.lwh_ke4.clientHeight)-44) {
          this.active = 1;
        } else if (pos.y < -(this.$refs.lwh_ke1.clientHeight+this.$refs.lwh_ke2.clientHeight+this.$refs.lwh_ke3.clientHeight+this.$refs.lwh_ke4.clientHeight)-44) {
          this.active = 2;
        }
      });
    });

    this.ajaxDetail();
    this.ajaxCouse();
  },
  activated() {},
  update() {},
  beforeRouteUpdate(to, from, next) {
    next();
  },
  methods: {
    async ajaxDetail() {
      let { data } = await this.$http.get(
        `/api/app/courseInfo/basis_id=${this.$route.query.id}`
      );

      this.lwh_list = data.data.teachers[0]
      this.CourseInfo = data.data.info;

    },
    async ajaxCouse() {
      let { data } = await this.$http.post("/api/app/courseComment", {
        id: this.$route.query.id,
        limit: 15,
        page: 1,
      });
      this.commentData = data.data.list;
      console.log(this.commentData);
    },
    //   分享
    onClickRight() {
      this.PopupIsShow = true;
    },
    //   返回首页
    onClickLeft() {
      this.$router.back(1);
    },
    // 跳转到提交订单页面
    async toPurchase() {
      if (this.CourseInfo.price == 0) {
        let {data} = await this.$http.post("https://www.365msmk.com/api/app/order/downOrder",{
          shop_id:this.CourseInfo.id,
          type:this.CourseInfo.course_type,
        })
        this.ajaxDetail()
        // this.$router.push("/study-detail");
      } else {
        this.$router.push("/Purchase");
      }
    },
    // 跳转到课程学习页面
    toStudy() {
      this.$router.push("/study-detail");
    },
    // 收藏
    async course(num) {
      // this.CourseInfo.is_collect = num;
      if (num == 0) {
        //取消收藏
        let { data } = await this.$http.put(
          `/api/app/collect/cancel/${this.CourseInfo.collect_id}/1`
        );
        if (data.code == 200) {
          Toast(data.msg);
          this.ajaxDetail();
        }
      } else {
        //收藏
        let { data } = await this.$http.post("/api/app/collect", {
          course_basis_id: this.CourseInfo.id,
          type: 1,
        });
        console.log(data);

        if (data.code == 200) {
          Toast(data.msg);

          this.ajaxDetail();
        }
      }
    },
    // 点击滚动置顶高度
    wsy_scr(index) {
      console.log(this.$refs.lwh_ke1.clientHeight+this.$refs.lwh_ke2.clientHeight) 
      this.active = index;
      if (index == 0) {
        this.wsy_bs.scrollTo(0, -(this.$refs.lwh_ke1.clientHeight+this.$refs.lwh_ke2.clientHeight)-27, 1000);
      } else if (index == 1) {
        this.wsy_bs.scrollTo(0, -(this.$refs.lwh_ke1.clientHeight+this.$refs.lwh_ke2.clientHeight+this.$refs.lwh_ke3.clientHeight)-44, 1000);
      } else if (index == 2) {
        this.wsy_bs.scrollTo(0, -(this.$refs.lwh_ke1.clientHeight+this.$refs.lwh_ke2.clientHeight+this.$refs.lwh_ke3.clientHeight+this.$refs.lwh_ke4.clientHeight)-54, 1000);
      }
    },
    // 跳转到视频
    jumpVideo(){
      this.$router.push(`/video?video_id=30929276&id=${this.CourseInfo.id}`)
    }
  },
  filters: {
    createTimeFilter(time) {
      let date = new Date(time);
      return formatDate(date, "yyyy-MM-dd hh:mm:ss"); //formatDate是从外部引入的函数，
    },
  },
  computed: {},
  watch: {
    commentData() {
      this.$nextTick(() => {
        this.wsy_bs.refresh();
      });
    },
  },
};
</script>

